* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #fefafaf3;
  padding: 10px;
  border-bottom: 1px solid rgb(35, 31, 31);

}

nav ul li {
  list-style: none;

}

.input input {
  padding: 5px;
  width: 500px;
  border-radius: 5px;
  height: 40px;
  margin: 4px;
  background-color: #f5f1f1;


}

.content {
  margin-left: 300px;
}

nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 50px;
}

.logo span {
  font-size: 1.5rem;
}

.logo {
  align-items: center;
  display: flex;
  gap: 5px;
}

nav ul i {
  color: rgb(62, 64, 65);
  font-size: 1rem;
  cursor: pointer;
}

/* 
 */
.sidebar {
  position: absolute;
  height: 100%;
  width: 250px;
  z-index: 1;
  top: 0;
  background: #fff;
  left: 0;
  bottom: 0;
  overflow: hidden;
  padding-top: 20px;
  margin-top: 69px;
  transition: width 0.2s linear;
  box-shadow: 0 20px 35px rgb(0, 0, 0, 0.1);
  font-family: sans-serif;
}

.sidebar ul {
  display: flex;
  flex-direction: column;
}

.sidebar ul li {
  list-style: none;
}



.sidebar ul a {
  padding: 6px 8px 6px 16px;
  text-decoration: none;
  line-height: 40px;
  font-size: 25px;
  color: #000;
  box-sizing: border-box;
}


.main-content {
  margin-left: 200px;
  padding: 20px;
}

.sidebar span {
  margin-left: 25px;
  font-size: 15px;
  color: #afaaaa;
}

.sidebar ul li a i {
  color: #afaaaa;
  font-size: 1rem;
}

.container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}

form {
  background-color: aqua;
  padding: 40px;
  border-radius: 5px;
}

form input {
  margin: 5px;
  height: 35px;
  width: 300px;
  border-radius: 5px;
}

form button {
  position: absolute;
  margin-left: 280px;
  margin-bottom: 280px;
  font-size: 1.5rem;
  background-color: green;
border-radius: 5px;
  width: 70px;
}

.darkmode{
  background-color: #384159;
  color: #fff;
}
.lightMode{
  background-color: #b1acac;
  color: #000;
}